#home {
  width: 90%;
  margin: auto;
  display: flex;
  padding-top: 70px;
  justify-content: space-between;
  border: 1px solid lightgrey;
}
#home > div {
  border: 1px solid lightgrey;
}
#home > div:nth-child(1) {
  width: 50%;
}
#home > div > img {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  border: 1px solid lightgrey;
}


#about {
  margin-top: 25px;
  padding-top: 70px;
  text-align: center;
  border: 1px solid lightgrey;
}


#skills {
  margin-top: 50px;
  padding-top: 70px;
  text-align: center;
  border: 1px solid lightgrey;
}
#skills-main {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 25px;
}
.skills-card {
  display: flex;
  padding: 4px 7px;
  align-items: center;
  border: 1px solid lightgrey;
}
.skills-card > img {
  height: 50px;
  margin-right: 10px;
}



#projects {
  margin-top: 50px;
  padding-top: 70px;
  text-align: center;
  border: 1px solid lightgrey;
}
#projects-main {
  width: 90%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 25px;
}
.project-card {
  padding: 10px 15px;
  border: 1px solid lightgrey;
}
.project-card-links {
  display: flex;
  justify-content: space-around;
}
.project-card-links > div {
  display: flex;
  align-items: center;
}
.project-card-links > div > *:nth-child(1) {
  margin-right: 5px;
}


#contact {
  margin-top: 50px;
  padding-top: 70px;
  text-align: center;
  border: 1px solid lightgrey;
}
#contact-main {
  display: flex;
  align-items: center;
  flex-direction: column;
  border: 1px solid lightgrey;
}
#contact-main > div {
  display: flex;
  width: 100px;
  align-items: center;
  margin-bottom: 10px;
  /* border: 1px solid lightgrey; */
}
#contact-main > div > *:nth-child(1) {
  margin-right: 10px;
}


#github-stats {
  width: 90%;
  margin: auto;
  margin-top: 25px;
  border: 1px solid lightgrey;
}
